<template>
  <div class="content_cinema">
    <div class="header">
      <div class="top">
        <div class="address" @click="$router.push('/main/city')">
          <p>{{ $store.state.cityName }}</p>
          <i class="iconfont icon-zhankai"></i>
        </div>
        <div>影院</div>
        <div class="iconfont search" @click="$router.push('/main/cinema/search')">&#xe649;</div>
      </div>
      <div class="bottom">
        <div v-for="(value,index) in list" @click="listshow(index)" :class="value.isselected?'active':''">
          {{ value.name }}
          <i class="iconfont icon-zhankai"></i>
        </div>
      </div>
    </div>
    <div class="footer" v-if="data.length">
      <ul>
        <li v-for="value in choosecity"
            @click="go_to(value)">
          <p class="p1">
            <span>{{ value.name }}</span>
            <span v-show="value.ticketTypes">{{ value.ticketTypes | type }}</span>
            <span>{{ value.address }}</span>
          </p>
          <p class="p2">
            <span v-show="list[1].name==='APP订票'">￥<span>{{ value.lowPrice | price }}</span>起</span>
            <i class="iconfont" v-show="list[1].name==='前台兑换'">&#xe601;</i>
            <span>{{ value.lang ? value.lang + 'km' : '距离未知' }}</span>
          </p>
        </li>
      </ul>
    </div>
    <div class="plan1" @touchmove.prevent v-show="list.some(value=>value.isselected)">
      <div class="plan1_v" v-show="list[0].isselected">
        <ul>
          <li v-for="value in citylist">
            <div :class="['plan1_c',(list[0].name===value?'plan1_active':'')]" @click="choose(value)">
              {{ value }}
            </div>
          </li>
        </ul>
      </div>
      <div class="plan2_v" v-show="list[1].isselected">
        <ul>
          <li>
            <div :class="['plan2_c',(list[1].name==='APP订票'?'plan2_active':'')]"
                 @click="choosepay('APP订票')">
              <i class="iconfont">&#xe70d;</i>
              <p class="c_value">APP订票</p>
            </div>
          </li>
          <li>
            <div :class="['plan2_c',(list[1].name==='前台兑换'?'plan2_active':'')]" @click="choosepay('前台兑换')">
              <i class="iconfont">&#xe70d;</i>
              <p class="c_value">前台兑换</p>
            </div>
          </li>
        </ul>
      </div>
      <div class="plan2_v" v-show="list[2].isselected">
        <ul>
          <li>
            <div :class="['plan2_c',(list[2].name==='最近去过'?'plan2_active':'')]" @click="chooseadd('最近去过')">
              <i class="iconfont">&#xe70d;</i>
              <p class="c_value">最近去过</p>
            </div>
          </li>
          <li>
            <div :class="['plan2_c',(list[2].name==='离我最近'?'plan2_active':'')]" @click="chooseadd('离我最近')">
              <i class="iconfont">&#xe70d;</i>
              <p class="c_value">离我最近</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <!-- <div class="plan1"></div> -->
    <!--  -->
    <!--  -->
    <!--  -->
  </div>
</template>

<script>
  import axios from "axios";
  import Vue from "vue"
  import {Toast} from "vant";
  import {getDistance, wgs84togcj02} from "@/utils/distance.js"

  Vue.use(Toast)

  Vue.filter("type", value => {
    let arr
    if (value) {
      arr = value.reduce((prev, value) => {
        return prev + value.name + "| "
      }, "");
      return arr.slice(0, arr.length - 2)
    } else {
      return ""
    }
  })
  Vue.filter("price", value => {
    return value / 100
  })
  export default {
    data() {
      return {
        list: [
          {
            name: "全城",
            isselected: false
          },
          {
            name: "APP订票",
            isselected: false
          },
          {
            name: "最近去过",
            isselected: false
          },
        ],
        data: [], //储存所有信息
        citylist: [], //储存所有的城市
        choosecity: [], //过滤后的数据
      }
    },
    created() {
      // this.$watch(() => {
      // 	return this.list[1].name
      // }, () => {

      // })
    },
    methods: {
      listshow(index) {
        this.list.forEach((value, i) => {
          if (i != index) {
            value.isselected = false
          }
        })
        this.list[index].isselected = !this.list[index].isselected
      },
      //提取城市，去重
      heavy() {
        let arr = ["全城"];
        this.data.forEach(value => {
          arr.push(value.districtName)
        })
        this.citylist = new Set(Array.from(arr))
      },
      //城市选择
      choose(value) {
        this.list[0].name = value;
        this.list[0].isselected = false;
        this.mapping();
      },
      //过滤方法
      mapping() {
        let condition = this.list[0].name;
        if (condition === "全城") {
          this.choosecity = this.data
        } else {
          this.choosecity = this.data.filter(val => val.districtName === condition);
        }
      },
      choosepay(value) {
        this.choosecity = [];
        if (value === "APP订票" && value !== this.list[1].name) {
          this.data_a();
        } else if (value === "前台兑换" && value !== this.list[1].name) {
          Toast.loading({
            message: 'loading...',
            forbidClick: true,
            duration: 0
          });
          axios({
            url: `https://m.maizuo.com/gateway?cityId=${this.$store.state.cityId}&ticketFlag=2&k=1584709`,
            headers: {
              'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16352477313746499972300801","bc":"110100"}',
              'X-Host': 'mall.film-ticket.cinema.list'
            }
          })
            .then(value => {
              this.data = value.data.data.cinemas;
              this.position(value)
              this.mapping();
              this.heavy()
              Toast.clear()
            })
        } else {
          this.choosecity = this.data
          this.heavy()
          this.mapping();
        }
        this.list[1].name = value;
        this.list[1].isselected = false;
      },
      chooseadd(value) {
        //以后要写上的方法
        if (value === "最近去过") {
          this.choosecity = this.data
        } else {
          for (let i = 0; i < this.choosecity.length - 1; i++) {
            for (let j = i + 1; j < this.choosecity.length; j++) {
              if (parseFloat(this.choosecity[i].lang) > parseFloat(this.choosecity[j].lang)) {
                let t = this.choosecity[i];
                this.choosecity[i] = this.choosecity[j];
                this.choosecity[j] = t;
              }
            }
          }
        }

        this.list[2].name = value;
        this.list[2].isselected = false;
        this.mapping();
      },
      data_a() {
        Toast.loading({
          message: 'loading...',
          forbidClick: true,
          duration: 0
        });
        axios({
          url: `https://m.maizuo.com/gateway?cityId=${this.$store.state.cityId}&ticketFlag=1&k=8955206`,
          headers: {
            'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16352477313746499972300801","bc":"110100"}',
            'X-Host': 'mall.film-ticket.cinema.list'
          }
        })
          .then(value => {
            this.data = value.data.data.cinemas;
            this.position(value)
            this.mapping();
            this.heavy()
            Toast.clear()
          })
      },
      position(value) {
        let e1 = this.$store.state.longitude
        let n1 = this.$store.state.latitude
        if (e1 && n1) {
          value.data.data.cinemas.forEach((v, i) => {
            let e2 = v.longitude
            let n2 = v.latitude
            this.data[i]["lang"] = getDistance(e1, n1, e2, n2).toFixed(1)
          })
        }
      },
      go_to(value) {
        if (this.list[1].name === "APP订票") {
          this.$router.push(`/main/cinema/${value.cinemaId}/film`)
        } else {
          this.$router.push(`/main/cinema/${value.cinemaId}`)
        }
      }

    },
    mounted() {
      this.data_a()
    },
    // watch: {
    // 	"list[1].name": function(){
    // 		axios({
    // 				url: "https://m.maizuo.com/gateway?cityId=110100&ticketFlag=2&k=1584709",
    // 				headers: {
    // 					'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16352477313746499972300801","bc":"110100"}',
    // 					'X-Host': 'mall.film-ticket.cinema.list'
    // 				}
    // 			})
    // 			.then()
    // 	}
    // }
  }
</script>

<style scoped lang="scss">
  .content_cinema {
    width: 100%;

    //选择面板
    .plan1 {
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.3);
      position: fixed;
      top: 5.875rem;

      .plan1_v {
        width: 100%;
        // height: 200px;
        background-color: #fff;

        ul {
          padding: 10px 0 0 10px;
          width: 100%;
          display: flex;
          flex-wrap: wrap;
          box-sizing: border-box;
          // justify-content: flex-start;

          li {
            width: 25%;
            height: 2.8125rem;

            .plan1_c {
              width: calc(100% - 15px);
              height: 1.875rem;
              display: flex;
              align-items: center;
              justify-content: center;
              font-size: 0.75rem;
              border: 1px solid rgba(210, 214, 220, .5);
              box-sizing: border-box;
              border-radius: 4px;
              color: #868686;
            }
          }
        }
      }

      .plan2_v {
        width: 100%;
        // height: 200px;
        background-color: #fff;

        ul {
          width: 100%;
          box-sizing: border-box;

          li {
            width: 100%;
            height: 2.75rem;
            background-color: #fff;
            color: #797d82;
            border-bottom: 1px solid rgba(210, 214, 220, .5);

            .plan2_c {
              width: 100%;
              height: 2.75rem;
              padding-left: 0.9375rem;
              display: flex;
              align-items: center;
              font-size: 0.8125rem;

              .iconfont {
                margin-top: 0.1875rem;
                margin-right: 0.5rem;
                opacity: 0;
              }
            }
          }
        }
      }

      //plan1选中
      .plan1_active {
        border: 0.0625rem solid #ff5f16 !important;
        color: #ff5f16 !important;
      }

      .plan2_active {
        color: #ff5f16;

        .iconfont {
          opacity: 1 !important;
        }
      }

    }

    li {
      list-style: none;
    }

    //根
    .header {
      width: 100%;
      background-color: #fff;
      position: fixed;
      top: 0;
      z-index: 100;

      .top {
        height: 2.75rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 0.0625rem solid #ededed;

        .address {
          // width: 3.125rem;
          max-width: 3.75rem;
          margin-left: 0.9375rem;
          font-size: 0.8125rem;
          display: flex;

          p {
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
          }

          .iconfont {
            font-size: 0.375rem;
            margin-top: 3px;
          }
        }

        .search {
          width: 3.125rem;
          margin-right: 0.9375rem;
          height: 100%;
          font-size: 1.25rem;
          display: flex;
          align-items: center;
          justify-content: flex-end;
        }
      }

      .bottom {
        width: 100%;
        display: flex;
        height: 3.0625rem;
        align-items: center;
        justify-content: space-between;
        font-size: 0.875rem;
        border-bottom: 0.0625rem solid #ededed;

        div {
          width: 100%;
          height: 100%;
          flex: 1;
          display: flex;
          justify-content: center;
          align-items: center;
          position: relative;
          transition: all 300ms ease-in-out;

          .iconfont {
            font-size: 6px;
            margin-top: 0.1875rem;
            margin-left: 0.125rem;
            transition: all 100ms ease-in-out;
          }
        }
      }
    }

    //选中效果
    .active {
      color: #ff5f16;

      i {
        transform: rotateX(180deg);
      }
    }

    .footer {
      width: 100%;
      position: fixed;
      top: 5.875rem;
      background-color: #fff;
      overflow: auto;
      height: calc(100% - 5.875rem - 50px);

      ul {
        width: 100%;
      }

      li {
        padding: 15px;
        width: 100%;
        box-sizing: border-box;
        border-bottom: 0.0625rem solid #ededed;
        display: flex;
      }

      .p1 {
        flex: 1;
        width: 0;
        display: flex;
        justify-content: space-between;
        align-content: flex-end;
        flex-direction: column;
        margin-right: 4.375rem;

        span {
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }

        span:first-of-type {
          font-size: 0.9375rem;
        }

        span:nth-of-type(2) {
          margin-top: 0.3125rem;
          font-size: 0.75rem;
          color: #797d82;
        }

        span:last-of-type {
          margin-top: 0.3125rem;
          font-size: 0.75rem;
          color: #797d82;

          span {
            font-size: 0.9375rem;
          }
        }
      }

      .p2 {
        display: flex;
        text-align: end;
        justify-content: center;
        align-content: flex-start;
        flex-direction: column;
        font-size: 0.625rem;
        width: 4.375rem;

        span:first-of-type {
          color: #ff5f16;

          span {
            color: #ff5f16;
            font-size: 0.9375rem;
          }
        }

        .iconfont {
          color: #797d82;
          text-align: center;
          padding-left: 1.5rem;
          box-sizing: border-box;
        }

        span:last-of-type {
          color: #797d82;
          font-size: 0.625rem;
          margin-top: 0.3125rem;
        }
      }

      .p3 {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 0.3125rem;

        span:first-of-type {
          width: 0;
          max-width: 60%;
          flex: 1;
          font-size: 0.75rem;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          color: #797d82;
        }
      }
    }
  }
</style>
